import Game from "/src/game";

class Index {
  constructor() {
    this.canvas = document.getElementById("gameScreen");
    this.ctx = this.canvas.getContext("2d");
    this.GAME_WIDTH = 800;
    this.GAME_HEIGHT = 600;
    this.game = new Game(this.GAME_WIDTH, this.GAME_HEIGHT);
    this.lastTime = 0;
  }
}
let index = new Index();
let lastTime = 0;

function gameLoop(timestamp) {
  let deltaTime = timestamp - lastTime;
  lastTime = timestamp;

  index.ctx.clearRect(0, 0, index.GAME_WIDTH, index.GAME_HEIGHT);
  index.game.update(deltaTime);
  index.game.draw(index.ctx);

  requestAnimationFrame(gameLoop);
}

export function NewGame() {
  index.game = new Game(index.GAME_WIDTH, index.GAME_HEIGHT);
}
requestAnimationFrame(gameLoop);
