import Brick from "/src/brick";

export function buildLevel(game, levelNumber) {
  let bricks = [];
  let levelObj = new Level(levelNumber);
  let level = levelObj.getLevel();

  level.forEach((row, rowIndex) => {
    row.forEach((brick, brickIndex) => {
      switch (brick) {
        case 1:
          let position = {
            x: 80 * brickIndex,
            y: 75 + 24 * rowIndex
          };
          let str = 1;
          bricks.push(new Brick(game, position, str));
          break;

        case 2:
          position = {
            x: 80 * brickIndex,
            y: 75 + 24 * rowIndex
          };
          str = 2;
          bricks.push(new Brick(game, position, str));
          break;

        case 3:
          position = {
            x: 80 * brickIndex,
            y: 75 + 24 * rowIndex
          };
          str = 3;
          bricks.push(new Brick(game, position, str));
          break;
        default:
          break;
      }
    });
  });
  return bricks;
}

class Level {
  constructor(levelNumber) {
    this.rows = levelNumber + 5;
  }
  getLevel() {
    let rows = new Array();
    for (let i = 0; i < this.rows; i++) {
      let line = new Array();
      for (let ii = 0; ii < 10; ii++) {
        line.push(Math.floor(Math.random() * 4));
      }
      rows.push(line);
    }
    return rows;
  }
}
