import Paddle from "/src/paddle";
import InputHandler from "/src/input";
import Ball from "/src/ball";
import Background from "/src/background";
import { NewGame } from "/src/index";
import Score from "/src/score";

import { buildLevel } from "/src/levels";

const GAMESTATE = {
  PAUSED: 0,
  RUNNING: 1,
  MENU: 2,
  GAMEOVER: 3,
  NEWLEVEL: 4,
  YOUWIN: 5
};

export default class Game {
  constructor(gameWidth, gameHeight) {
    this.highschore = 0;
    this.gameWidth = gameWidth;
    this.gameHeight = gameHeight;
    this.gamestate = GAMESTATE.MENU;
    this.paddle = new Paddle(this);
    this.ball = new Ball(this);
    this.gameObjects = [];
    this.bricks = [];
    this.lives = 4;
    this.score = new Score();
    this.imglives = document.getElementById("img_lives");
    this.imgball = document.getElementById("img_ball");
    this.bg = Background.newImg(this.gameWidth, this.gameHeight);
    this.bgMenu = Background.newImg(this.gameWidth, this.gameHeight);
    this.gameover = document.getElementById("img_gameover");
    this.youwin = document.getElementById("img_youwin");
    this.currentLevel = 0;

    new InputHandler(this.paddle, this);
  }

  start() {
    if (
      this.gamestate !== GAMESTATE.MENU &&
      this.gamestate !== GAMESTATE.NEWLEVEL &&
      this.gamestate !== GAMESTATE.GAMEOVER
    )
      return;

    this.bricks = buildLevel(this, this.currentLevel);
    this.ball.reset();
    this.gameObjects = [this.ball, this.paddle];

    this.gamestate = GAMESTATE.RUNNING;
  }

  update(deltaTime) {
    if (this.lives === 0) this.gamestate = GAMESTATE.GAMEOVER;

    if (
      this.gamestate === GAMESTATE.PAUSED ||
      this.gamestate === GAMESTATE.MENU
    )
      return;

    if (this.bricks.length === 0) {
      this.currentLevel++;
      this.gamestate = GAMESTATE.NEWLEVEL;
      this.score.FinishLevel();
      this.start();
    }

    [...this.gameObjects, ...this.bricks].forEach((object) =>
      object.update(deltaTime)
    );

    this.bricks = this.bricks.filter((brick) => !brick.markedForDeletion);
  }
  draw(ctx) {
    ctx.drawImage(this.bg, 0, 0, this.gameWidth, this.gameHeight);
    ctx.rect(0, 0, this.gameWidth, 50);
    ctx.fillStyle = "rgba(0,0,0,0.60)";
    ctx.fill();
    ctx.drawImage(this.imglives, 0, 0, 100, 50);
    for (let i = 2; i <= this.lives; i++) {
      ctx.drawImage(this.imgball, i * 20 + 60, 17, 16, 16);
    }

    //put score here

    ctx.font = "20px Arial";
    ctx.fillStyle = "white";
    ctx.fillText("Score: " + this.score, this.gameWidth - 100, 35);
    [...this.gameObjects, ...this.bricks].forEach((object) => object.draw(ctx));

    //Pause Screen
    if (this.gamestate === GAMESTATE.PAUSED) {
      ctx.rect(0, 0, this.gameWidth, this.gameHeight);
      ctx.fillStyle = "rgba(0,0,0,0.5)";
      ctx.fill();

      ctx.font = "30px Arial";
      ctx.fillStyle = "white";
      ctx.textAlign = "center";
      ctx.fillText("Paused", this.gameWidth / 2, this.gameHeight / 2);
    }

    //Menu Screen
    if (this.gamestate === GAMESTATE.MENU) {
      ctx.drawImage(this.bgMenu, 0, 0, this.gameWidth, this.gameHeight);
      ctx.rect(0, 0, this.gameWidth, this.gameHeight);
      ctx.fillStyle = "rgba(0,0,0,0.5)";
      ctx.fill();

      ctx.font = "40px Arial";
      ctx.fillStyle = "white";
      ctx.textAlign = "center";
      ctx.fillText("Brick Breaker", this.gameWidth / 2, this.gameHeight / 2);
      ctx.font = "20px Arial";
      ctx.fillStyle = "white";
      ctx.fillText(
        "Created by Jake Murray",
        this.gameWidth / 2,
        this.gameHeight - 20
      );
    }

    //Game over Screen
    if (this.gamestate === GAMESTATE.GAMEOVER) {
      ctx.drawImage(this.bgMenu, 0, 0, this.gameWidth, this.gameHeight);
      ctx.rect(0, 0, this.gameWidth, this.gameHeight);
      ctx.fillStyle = "rgba(0,0,0,0.5)";
      ctx.fill();

      ctx.font = "30px Arial";
      ctx.fillStyle = "white";
      ctx.textAlign = "center";
      ctx.fillText(this.score, this.gameWidth / 2, this.gameHeight / 2 - 100);
      ctx.fillText("Game Over", this.gameWidth / 2, this.gameHeight / 2);
      ctx.font = "20px Arial";
      ctx.fillStyle = "white";
      ctx.fillText(
        "Press ENTER to restart",
        this.gameWidth / 2,
        this.gameHeight - 20
      );

      //if NEW HIGHSCORE
      if (this.score > this.highschore) {
        this.highschore = this.score;
        ctx.font = "40px Arial";
        ctx.fillStyle = "white";
        ctx.textAlign = "center";
        ctx.fillText(
          "New Highscore!",
          this.gameWidth / 2,
          this.gameHeight / 2 - 200
        );
      }
    }
    if (this.gamestate === GAMESTATE.YOUWIN) {
      ctx.drawImage(this.youwin, 0, 0, this.gameWidth, this.gameHeight);
    }
  }

  restart() {
    NewGame();
    this.score.GameOver();
  }

  togglePause() {
    console.log(this.gamestate);
    if (this.gamestate === GAMESTATE.MENU) return;
    if (this.gamestate === GAMESTATE.GAMEOVER) {
      this.restart();
    }
    if (this.gamestate === GAMESTATE.PAUSED) {
      this.gamestate = GAMESTATE.RUNNING;
    } else {
      this.gamestate = GAMESTATE.PAUSED;
    }
  }
}
