export default class Score {
  constructor() {
    if (Score._instance) {
      return Score._instance;
    }
    Score._instance = this;
    this.score = 0;
  }
  FinishLevel() {
    this.score += 10000;
  }
  BreakBrick() {
    this.score += 100;
  }
  HitBrick() {
    this.score += 10;
  }
  GameOver() {
    this.score = 0;
  }
  toString() {
    return this.score;
  }
}
